.hero{
    height: 100vh;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
    display: flex;
}


.left{
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex: 1;
    gap: 20px;
    padding-left: 140px;
    padding-top: 20px;
    line-height: 1.1;
}

.left h2{
    color: #090909;
    font-size: 20px;
    font-weight: 600;
}

.hero p{
    color: #171717;
    font-size: 40px;
    font-weight: 700;
}

.hand {
    display: flex;
    align-items: center;
    gap: 20px;
}

.hand img{
    width: 50px;
}


.right img{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding-top: 2%;
}

.Btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    cursor: pointer;
}